<template>
    <div class="index">      
      <span class="index-col" v-for="(item, index) in menus" :key="item.id" @click="jumpPages(item.id)">
        <div class="index-option">
          <div class="option-icon" id="icon-repair" v-if="item.id == 240">
            <svg class="icon" aria-hidden="true">
                <use xlink:href="#icon-dingdan"></use>
            </svg>
          </div>
          <div class="option-icon" id="icon-water" v-else-if="item.id >= 241 && item.id <= 243">
            <svg class="icon" aria-hidden="true">
                <use xlink:href="#icon-yibiaopan"></use>
            </svg>
          </div>
          <div class="option-icon" id="icon-device" v-else-if="item.id == 244">
            <svg class="icon" aria-hidden="true">
                <use xlink:href="#icon-shebeiguanli"></use>
            </svg>
          </div>
          <div class="option-icon" id="icon-task" v-else-if="item.id == 245">
            <svg class="icon" aria-hidden="true">
                <use xlink:href="#icon-loupan1"></use>
            </svg>
          </div>
          <div class="option-text" v-if="item.id == 240">报修投诉</div>
          <div class="option-text" v-else-if="item.id >= 241 && item.id <= 243">抄表</div>
          <div class="option-text" v-else-if="item.id == 244" >设备管理</div>
          <div class="option-text" v-else-if="item.id == 245">收楼任务</div>
        </div>
      </span>
    </div>
</template>
<script>
import { index } from "./../../assets/js/api.js";
import computed from "./../../assets/js/computed.js";

export default {
  data() {
    return {
      menus: {}
    };
  },
  methods: {
    jumpPages(id) {
      switch (id) {
        case 241:
          this.$router.push({
            path: "/RepairList"
          });
          break;
        case 242:
          this.$router.push({
            path: "/RepairList"
          });
          break;
        case 243:
          this.$router.push({
            path: "/RepairList"
          });
          break;
        case 244:
          this.$router.push({
            path: "/RepairList"
          });
          break;
        case 245:
          this.$router.push({
            path: "/RepairList"
          });
          break;
        default:
          this.$router.push({
            path: "/RepairList"
          });
          break;
      }
    }
  },
  store: this.$store,
  computed,
  mounted() {
    const vm = this;
    index.info({
      params: {
        userId: vm.userId
      },
      fn: data => {
        document.querySelector(".mint-header-title").innerHTML =
          data.manageName;
        let menus = [],
          repeat = true;
        for (let i = 0; i < data.menus.length; i++) {
          if (data.menus[i].id >= 241 && data.menus[i].id <= 243 && repeat) {
            menus.push(data.menus[i]);
            repeat = false;
          } else if (data.menus[i].id < 241 || data.menus[i].id > 243) {
            menus.push(data.menus[i]);
          }
        }
        vm.menus = menus;
      }
    });
  }
};
</script>
<style lang="less" scoped>
.index {
  width: 100%;
  text-align: center;
  .index-col {
    width: 50%;
    border-radius: 10px;
    .index-option {
      width: 100%;
      height: 580px/@p;
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      .option-icon {
        width: 180px/@p;
        height: 180px/@p;
        border-radius: 5px;
        color: #fff;
        display: flex;
        align-items: center;
        justify-content: center;
        .icon {
          font-size: 40px;
        }
      }
      .option-text {
        margin-top: 15px;
        font-size: 52px/@p;
      }
      #icon-repair {
        background: linear-gradient(
          to right,
          @repairIconLeft 0%,
          @repairIconRight 100%
        );
      }
      #icon-water {
        background: linear-gradient(
          to right,
          @waterIconLeft 0%,
          @waterIconRight 100%
        );
      }
      #icon-relectric {
        background: linear-gradient(
          to right,
          @relectricIconLeft 0%,
          @relectricIconRight 100%
        );
      }
      #icon-gas {
        background: linear-gradient(
          to right,
          @gasIconLeft 0%,
          @gasIconRight 100%
        );
      }
      #icon-device {
        background: linear-gradient(
          to right,
          @deviceIconLeft 0%,
          @deviceIconRight 100%
        );
      }
      #icon-task {
        background: linear-gradient(
          to right,
          @taskIconLeft 0%,
          @taskIconRight 100%
        );
      }
    }
  }
  &:nth-child(1) {
    border: 10px solid @brGray;
  }
}
</style>



