'use strict'
/**
 * @file Vuex 状态封装
 * @author dj
 */
import Vue from 'vue';
import Vuex from 'vuex';

Vue.use(Vuex);

const store = new Vuex.Store({
    //数据源申明
    state: {
        userId: 0,
        token: '',
        pageTitle: '',
    },
    //写入缓存并同步state
    mutations: {
        userId: (state, val) => {
            state.userId = val;
            window.localStorage.setItem('userId', val);
        },
        token: (state, val) => {
            state.token = val;
            window.localStorage.setItem('token', val);
        },
    }
})

export default store


