<template>
    <div class="login">
        <div class="login-logo">
          <svg class="icon" aria-hidden="true">
            <use xlink:href="#icon-lansheng_home"></use>
          </svg>
        </div>
        <div class="login-title">揽胜家园物业管理系统</div>
        <div class="login-form">
          <form @submit.prevent="userLogin()">
            <div class="login-form-input">
              <span class="input-icon">
                <svg class="icon" aria-hidden="true">
                  <use xlink:href="#icon-ERP_shenqingren"></use>
                </svg>
              </span>
              <input v-model="login_name" type="text" placeholder="请输入员工账号" @input="isInput">
            </div>
            <div class="login-form-input">
              <span class="input-icon">
                <svg class="icon" aria-hidden="true">
                  <use xlink:href="#icon-ERP_mima"></use>
                </svg>
              </span>
              <input v-model="password" id="pwd" type="password" placeholder="请输入员工账号" @input="isInput">
              <svg class="icon input-show" aria-hidden="true" @click.stop="pwdShow">
                <use xlink:href="#icon-yincang" id="showIcon"></use>
              </svg>
            </div>
            <div class="login-form-input border-0">
              <button type="submit" :disabled="isLogin" :class="!isLogin ? 'active' : ''">登录</button>
            </div>
          </form>
        </div>
    </div>
</template>

<script>
import { login } from "@/assets/js/api.js";

export default {
  data() {
    return {
      banner: "",
      login_name: "",
      password: "",
      isShow: false,
      isLogin: true
    };
  },
  methods: {
    //账号输入
    isInput() {
      if (this.login_name != "" && this.password != "") {
        this.isLogin = false;
      } else {
        this.isLogin = true;
      }
    },
    //登录
    userLogin() {
      const vm = this;
      login.verify({
        params: {
          login_name: vm.login_name,
          password: vm.password
        },
        fn: data => {
          this.$store.commit("userId", data.UserId);
          this.$store.commit("token", data.token);
          this.$router.replace({ path: "/" });
        }
      });
    },
    //显示隐藏密码
    pwdShow() {
      if (!this.isShow) {
        document.querySelector("#pwd").setAttribute("type", "text");
        document
          .querySelector("#showIcon")
          .setAttribute("xlink:href", "#icon-xianshi");
      } else {
        document.querySelector("#pwd").setAttribute("type", "password");
        document
          .querySelector("#showIcon")
          .setAttribute("xlink:href", "#icon-yincang");
      }
      this.isShow = !this.isShow;
    }
  },
  store: this.$store
};
</script>

<style lang="less" scoped>
.login {
  width: 100%;
  .login-logo {
    width: 75px;
    height: 75px;
    border-radius: 75px;
    margin: 0 auto;
    background-color: @mainColor;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-top: 45px;
    .icon {
      color: #fff;
      font-size: 50px;
    }
  }
  .login-title {
    text-align: center;
    font-size: 19px;
    margin-top: 20px;
    letter-spacing: 1px;
  }
  .login-form {
    width: 100%;
    margin-top: 110px;
    .login-form-input {
      width: 90%;
      margin: 20px auto;
      border-bottom: 1px solid @brGray;
      display: flex;
      align-items: center;
      position: relative;
      > .input-icon {
        flex: 1;
        color: @gray;
        font-size: 22px;
        text-align: center;
      }
      > input {
        flex: 12;
        height: 35px;
        border: 0;
        padding-left: 20px;
        font-size: 16px;
        display: inline-block;
      }
      > button {
        width: 100%;
        height: 130px/@p;
        font-size: 18px;
        color: @white;
        background-color: @aqua;
        border: 0;
        border-radius: 130px/@p;
        margin-top: 30px;
      }
      .active {
        background-color: @green;
      }
      .input-show {
        font-size: 20px;
        margin-right: 10px;
      }
    }
  }
}
</style>


