/**
 * @file router
 * @author dj
 */

import Vue from 'vue';
import Router from 'vue-router';
import Index from './pages/Index/Index';
import Login from './pages/Login/Login';
import RepairList from './pages/Repair/RepairList';
import RepairInfo from './pages/Repair/RepairInfo';

Vue.use(Router);
export function createRouter() {
    const router = new Router({
        // history 模式，需要服务器后端配合做路由代理，将所有的前端路由同步代理到 /
        mode: 'history',
        scrollBehavior(to, from, savedPosition) {
            if (savedPosition) {
                return savedPosition;
            }
            return {
                x: 0,
                y: 0
            };
        },
        routes: [{
            path: '/',
            name: "揽胜家园",  //页面标题
            component: Index,
            meta: {
                keepAlive: true // 缓存页面
            }
        }, {
            path: '/Login',
            name: "用户登录",
            component: Login,
        }, {
            path: '/RepairList',
            name: "报修投诉",
            component: RepairList,
        }, {
            path: '/RepairInfo',
            name: "订单详情",
            component: RepairInfo,
        }]
    });

    return router;
}



