'use strict'
/**
 * @file 接口模块
 * @author dj
 */

import $api from "./request.js"

const login = {
    logoImage(obj) {
        return $api.post({
            url: '/propertyStaff/login.do',
            fn: obj.fn
        })
    },
    verify(obj) {
        return $api.post({
            url: '/propertyStaff/login/verification.do',
            params: obj.params,
            fn: obj.fn
        })
    }
};

const index = {
    info(obj) {
        return $api.post({
            url: '/propertyStaff/index.do',
            params: obj.params,
            fn: obj.fn
        })
    }
}

export {
    login,
    index
}


