<template>
  <div id="app">
    <mt-header :title="pageTitle" :fixed="true">
      <router-link to="" slot="left" v-if="isBackShow" @click.native="backPages">
        <mt-button icon="back">返回</mt-button>
      </router-link>
      <!-- <mt-button icon="more" slot="right" @click.native="morePages"></mt-button> -->
    </mt-header>
    <keep-alive>
      <router-view class="router-view" v-if="$route.meta.keepAlive" />
    </keep-alive>

    <router-view class="router-view" v-if="!$route.meta.keepAlive" />
  </div>
</template>
<script>
export default {
  name: "app",
  data() {
    return {
      pageTitle: "",
      isBackShow: false
    };
  },
  methods: {
    backPages() {
      this.$router.go(-1);
    },
    changeTitle(val) {
      this.pageTitle = val;
    },
    morePages() {
    }
  },
  mounted() {
    //全局路由守卫
    this.$router.afterEach((to, from) => {
      // 页面标题
      if (typeof to.name !== "undefined") {
        this.pageTitle = to.name;
      }

      // 返回键显示
      if (to.fullPath == "/") {
        this.isBackShow = false;
      } else {
        this.isBackShow = true;
      }
    });
  }
};
</script>




